/* Maintain existing styles */

.nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: black;
  padding: 0 20px;
  opacity: 70%;
}

.leftSide,
.rightSide {
  display: flex;
  align-items: center;
  opacity: 100%;
}

.rightSide {
  /* Adjust spacing and styles as needed */
  padding: 0 20px;
  opacity: 100%;
}

/* .logo {
  width: 35px;
  height: 35px;
} */

.sign {
  width: 180px;
  height: 40px;
  object-fit: cover;
  object-position: 100% 50%;
}

.navLink a{
  color: white; /* Adjust color as desired */
  text-decoration: none; /* Remove default underline */
  font-family: 'Poppins', sans-serif;
  font-size: 12px;
  padding: 0 10px;
  margin-right: 10px;
}

/* New styles for hover and active link states */

.nav .navLink a:hover,
.nav .navLink .active a{
  background-color: white; /* White background */
  color: black;  /* Black text */
  border-radius: 20px; /* Add rounded corners */
  padding: 5px 10px;  /* Add some padding for visual balance */
  transition: all 0.2s ease-in-out; 
  font-size: 12px;
  font-weight: bold;
  opacity: 90%;
}
